import { useState, useEffect } from 'react'

import { Row, Col, Stack, Button, Badge } from 'react-bootstrap'

export type TriggersProps = {
  events: string[],
  onTrigger?: (event: string) => void,
}

export default function Triggers({ events, onTrigger }: TriggersProps): JSX.Element {
  const [history, setHistory] = useState<string[]>([''])
  useEffect(() => setHistory(['']), [events])

  return (
    <Col className={'p-1 pt-2'} xs={'auto'}>
      <Row xs={'auto'} className={'p-0 ps-1 pe-1'}>
        <Stack direction={'horizontal'} gap={2} className={'ps-1 pe-1 pb-1 justify-content-end overflow-hidden'}>
          {history.map((event, i) => {
            const isLast = i + 1 === history.length
            return (
              <Badge
                key={i}
                bg={isLast ? 'primary' : 'secondary'}
                className={'ms-0'}
              >
                {event}
              </Badge>
            )
          })}
        </Stack>
      </Row>
      <Row className={'p-1'}>
        {events.map(event => (
          <Col key={event} className={'p-1'} xs={'auto'}>
            <Button
              variant={'primary'}
              onClick={() => {
                if (onTrigger) onTrigger(event)
                setHistory([...history, event].slice(-64))
              }}
            >
              <strong>{event}</strong>
            </Button>
          </Col>
        ))}
      </Row>
    </Col>
  )
}
