import { HTMLProps, useState, useMemo } from 'react'
import { Row, Col, ListGroup, Button } from 'react-bootstrap'

import { Program, ValueHandles, ValueHandle } from 'dms'

import { useTheme } from './PersistentStateContext'
import Canvas from "./Canvas"

export type ValuesProps = {
  program: Program,
  handles: ValueHandles,
  counter: number,
  failure: (err: unknown) => void,
}

export default function Values({ program, handles, counter, failure }: ValuesProps): JSX.Element {
  return (
    <ListGroup>
      {mapRange(handles.len(), i => {
        const handle = handles.get(i)
        return <Value key={i} {...{ program, handle, counter, failure }} />
      })}
    </ListGroup>
  )
}

function mapRange<T>(len: number, f: (i: number) => T): T[] {
  const values: T[] = []
  for (let i = 0; i < len; i++) {
    values.push(f(i))
  }
  return values
}

type ValueProps = {
  program: Program,
  handle: ValueHandle,
  counter: number,
  failure: (err: unknown) => void,
}

function Value({ program, handle, counter, failure }: ValueProps): JSX.Element {
  const { theme } = useTheme()

  const { name, value, children } = useMemo(() => {
    const name = handle.is_dynamic() ? `(at ${handle.name()})` : handle.name()
    let value: string | BigInt64Array
    const children = handle.children()

    if (handle.is_dynamic()) {
      try {
        const current = program.current_value_of_dynamic(handle)
        if (current) {
          value = current.pixels() ?? current.show()
        } else {
          value = "(inactive)"
        }
      } catch (err) {
        failure(err)
        value = "(error)"
      }
    } else {
      value = handle.pixels() ?? handle.show()
    }

    return { name, value, children }
  }, [program, handle, counter, failure])

  const [showChildren, setShowChildren] = useState(false)

  return (
    <ListGroup.Item variant={'light'} className={'p-1'}>
      <Col className={'ps-1'}>
        <Row>
          <Col xs={{ span: 4 }}>
            <strong>
              <code className={theme === 'dark' ? 'text-light' : 'text-dark'}>
                {name}
              </code>
            </strong>
          </Col>
          {typeof value === 'string'
            ? <Col><code className={theme === 'dark' ? 'text-light' : 'text-dark'}>{value}</code></Col>
            : <Col><Pixels width={'300'} height={'300'} pixels={value} /></Col>}
          {children && children.len() > 0 && (
            <Col xs={'auto'}>
              <Button
                size={'sm'}
                variant={theme === 'dark' ? 'dark' : 'light'}
                className={'p-0 ps-2 pe-2'}
                onClick={() => setShowChildren(!showChildren)}
              >
                {showChildren ? '' : ''}
              </Button>
            </Col>
          )}
        </Row>
        {showChildren && children && (
          <Row className={'ms-1 mt-1'}>
            <Values {...{ program, handles: children, counter, failure }} />
          </Row>
        )}
      </Col>
    </ListGroup.Item>
  )
}

function Pixels(props: HTMLProps<HTMLCanvasElement> & { pixels: BigInt64Array }): JSX.Element {
  const { pixels, ...rest } = props
  return (
    <Canvas {...rest} sourceData={pixels} onDraw={(c, pxs) => {
      const ctx = c.getContext('2d')
      if (!ctx) return
      ctx.clearRect(0, 0, c.width, c.height)

      const numPixels = Math.floor(pxs.length / 5)
      if (numPixels <= 0) return

      let [minX, minY, maxX, maxY] = [+Infinity, +Infinity, -Infinity, -Infinity]
      for (let i = 0; i < numPixels; i++) {
        const [x, y] = [pxs[i*5 + 3], pxs[i*5 + 4]]
        if (x < minX) minX = Number(x)
        if (y < minY) minY = Number(y)
        if (x > maxX) maxX = Number(x)
        if (y > maxY) maxY = Number(y)
      }
      maxX++
      maxY++
      const fitSquareX = Math.min(0, ((maxX-minX) - (maxY-minY)) / 2)
      const fixSquareY = Math.min(0, ((maxY-minY) - (maxX-minX)) / 2)
      minX += fitSquareX
      minY += fixSquareY
      maxX -= fitSquareX
      maxY -= fixSquareY
      minX -= 0.2
      minY -= 0.2
      maxX += 0.2
      maxY += 0.2

      for (let i = 0; i < numPixels; i++) {
        const [r, g, b, x, y] = Array.from(pxs.slice(5*i, 5*i + 5)).map(Number)
        const x0 = (x - minX) / (maxX - minX) * c.width
        const y0 = (y - minY) / (maxY - minY) * c.height
        const w = c.width / (maxX - minX)
        const h = c.height / (maxY - minY)
        ctx.fillStyle = `rgb(${r},${g},${b})`
        ctx.fillRect(x0, y0, w, h)
      }
    }} />
  )
}
