import { useRef, useEffect, HTMLProps } from 'react'

type CanvasProps<T> = HTMLProps<HTMLCanvasElement> & {
  sourceData: T,
  onDraw: (c: HTMLCanvasElement, d: T) => void,
}

export default function Canvas<T>(props: CanvasProps<T>): JSX.Element {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const { sourceData, onDraw, ...rest } = props
  useEffect(() => {
    if (!canvasRef.current) return
    onDraw(canvasRef.current, sourceData)
  }, [sourceData, onDraw])
  return <canvas ref={canvasRef} {...rest} />
}
