import { useState, useMemo, useEffect } from 'react'
import { Container, Row, Col, Navbar, Button, Alert, Dropdown } from 'react-bootstrap'

import { useTheme, useFile, useCode } from './PersistentStateContext'
import Editor from './Editor'
import Triggers from './Triggers'
import Values from './Values'

import { Program, ValueHandles, Failure } from 'dms'

const repo: ({ file: string, name: string } | null)[] = [
  { file: 'tutorial0.dmp', name: 'Tutorial 0: Introduction' },
  { file: 'tutorial1.dmp', name: 'Tutorial 1: Definitions' },
  { file: 'tutorial2.dmp', name: 'Tutorial 2: Arithmetics' },
  { file: 'tutorial3.dmp', name: 'Tutorial 3: Booleans & conditions' },
  { file: 'tutorial4.dmp', name: 'Tutorial 4: Pixels' },
  { file: 'tutorial5.dmp', name: 'Tutorial 5: Lambda' },
  { file: 'tutorial6.dmp', name: 'Tutorial 6: Let' },
  { file: 'tutorial7.dmp', name: 'Tutorial 7: Pairs & lists' },
  { file: 'tutorial8.dmp', name: 'Tutorial 8: Events' },
  { file: 'tutorial9.dmp', name: 'Tutorial 9: After' },
  { file: 'tutorial10.dmp', name: 'Tutorial 10: Select' },
  { file: 'tutorial11.dmp', name: 'Tutorial 11: Loop' },
  { file: 'tutorial12.dmp', name: 'Tutorial 12: Join' },
  { file: 'tutorial13.dmp', name: 'Tutorial 13: Common functional patterns' },
  { file: 'tutorial14.dmp', name: 'Tutorial 14: Crazy event stuff' },
  null,
  { file: 'snake.dmp', name: 'Example: Snake' },
  null,
  { file: 'playground.dmp', name: 'My Playground' }
]

export default function App() {
  const { theme, setTheme } = useTheme()
  const { file, setFile, reloadFile } = useFile()
  const { code, setCode } = useCode()
  const [program, setProgram] = useState<Program | null>(null)
  const [error, setError] = useState<Failure | string | null>(null)

  useEffect(() => {
    setFile('playground.dmp')
  }, [])

  useEffect(() => {
    setError(null)
  }, [code])

  const failure = (err: unknown) => {
    if (err instanceof Failure) {
      setError(err)
    } else {
      setError(String(err))
    }
  }

  const run = () => {
    try {
      const newProgram = Program.new(code)
      setProgram(newProgram)
      setError(null)
    } catch (err) {
      failure(err)
    }
  }

  const events = useMemo(() => {
    if (!program) return undefined
    const events: string[] = []
    for (let i = 0; i < program.num_base_events(); i++) {
      events.push(program.base_event(i))
    }
    return events
  }, [program])

  const [handles, setHandles] = useState<ValueHandles | undefined>(undefined)
  useEffect(() => {
    setHandles(program?.globals())
  }, [program])

  const [counter, setCounter] = useState(0)
  const onTrigger = (event: string) => {
    if (!program) return
    try {
      program.trigger(event)
      setCounter(counter + 1)
    } catch (err) {
      failure(err)
    }
  }

  return (
    <Container fluid className={'bg-body vh-100 d-flex flex-column'} data-bs-theme={theme}>
      <Row>
        <Navbar className={'ps-3'}>
          <Navbar.Brand>
             Dynamic Modal Playground
          </Navbar.Brand>
          <Navbar.Collapse>
            <Row className={'gx-2'}>
              <Col xs={'auto'}>
                <Button variant={'success'} onClick={run}> Run</Button>
              </Col>
              <Col xs={'auto'}>
                <Dropdown>
                  <Dropdown.Toggle variant={theme === 'dark' ? 'dark' : 'light'} className={'ps-2 pe-2'}>
                    {file}
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    {repo.map((item, i) => {
                      if (item === null) return <Dropdown.Divider key={i} />
                      return (
                        <Dropdown.Item
                          key={i}
                          active={item.file === file}
                          onClick={() => setFile(item.file)}
                        >
                          <Row className={'align-items-center'}>
                            <Col xs={'auto'}>
                              {item.name}
                            </Col>
                            {item.file === file && (
                              <Col xs={'auto'} onClick={reloadFile}>
                                <Button variant={'warning'} size={'sm'}>
                                   Original
                                </Button>
                              </Col>
                            )}
                          </Row>
                        </Dropdown.Item>
                      )
                    })}
                  </Dropdown.Menu>
                </Dropdown>
              </Col>
            </Row>
          </Navbar.Collapse>
          <Navbar.Collapse className={'justify-content-end me-2'}>
            <Button
              variant={theme === 'dark' ? 'dark' : 'light'}
              className={'pt-0 pb-0'}
              onClick={() => {
                if (theme === 'dark') setTheme('light')
                if (theme === 'light') setTheme('dark')
              }}
            >
              <h3 className={'m-0'}></h3>
            </Button>
          </Navbar.Collapse>
        </Navbar>
      </Row>

      <Row className={'justify-content-center flex-grow-1 overflow-auto'}>
        <Col className={'h-100'} md={{ span: 6 }}>
          <Editor
            code={code}
            highlightError={error instanceof Failure ? error : undefined}
            onChange={newCode => {
              setCode(newCode)
            }}
          />
        </Col>

        <Col className={'d-flex flex-column h-100'} md={{ span: 6}}>
          {events && <Triggers {...{ events, onTrigger }} />}
          {program && handles && (
            <Row className={'flex-grow-1 overflow-auto'}>
              <Col>
                <Values {...{ program, handles, counter, failure }} />
              </Col>
            </Row>
          )}
        </Col>
      </Row>

      {error && (
        <Row>
          <Col>
            <Alert variant={'warning'}>
              {typeof error === 'string'
                ? error
                : error.message()}
            </Alert>
          </Col>
        </Row>
      )}
      </Container>
  )
}
