import { useMemo } from 'react'

import AceEditor from 'react-ace'
import 'ace-builds/src-noconflict/theme-dracula'
import 'ace-builds/src-noconflict/theme-github'
import './highlighting'

import 'ace-builds/src-noconflict/mode-scheme'

import { useTheme } from './PersistentStateContext'

export type EditorProps = {
  code: string,
  highlightError?: {
    start: number,
    end: number,
  },
  onChange?: (newCode: string) => void,
}

export default function Editor({ code, highlightError, onChange }: EditorProps): JSX.Element {
  const { theme } = useTheme()

  const marker = useMemo(() => {
    if (!highlightError) return undefined
    const start = lineAndColumn(code, highlightError.start)
    const end = lineAndColumn(code, highlightError.end)
    return {
      startRow: start.line,
      startCol: start.column,
      endRow: end.line,
      endCol: end.column,
      className: 'syntax-error-marker',
      type: 'text',
    } as const
  }, [code, highlightError])

  return (
    <AceEditor
      style={{ fontFamily: 'monospace' }}
      mode={'dmp'}
      value={code}
      markers={marker ? [marker] : []}
      highlightActiveLine={!highlightError}
      scrollMargin={[0, 500]}
      onChange={onChange}
      theme={theme === 'dark' ? 'dracula' : 'github'}
      fontSize={16}
      tabSize={2}
      width={'100%'}
      height={'100%'}
      name={'DYNAMIC_MODAL_EDITOR'}
      editorProps={{ $blockScrolling: true }}
    />
  )
}

function lineAndColumn(code: string, position: number) {
  let line = 0
  let column = 0
  let i = 0
  for (const char of code) {
    if (i >= position) return { line, column }
    column += 1
    if (char === '\n') {
      line += 1
      column = 0
    }
    i += 1
  }
  return { line, column }
}
