import { createContext, useContext, useState, useEffect, PropsWithChildren } from 'react'

export type Theme = 'light' | 'dark'

export type PersistentState = {
  theme: Theme,
  file?: string,
  code: string,
}

function checkTheme(theme: unknown): Theme | undefined {
  if (theme === 'dark') return theme
  if (theme === 'light') return theme
  return undefined
}

type ContextData = {
  state: PersistentState,
  setState: (newState: PersistentState) => void,
  reloadFile: () => void,
}

const Context = createContext<ContextData>({
  state: {
    theme: 'dark',
    code: '',
  },
  setState: () => {},
  reloadFile: () => {},
})

export function PersistentStateProvider({ children }: PropsWithChildren<{}>): JSX.Element {
  const [state, setState] = useState<PersistentState>({
    theme: checkTheme(window.localStorage.getItem('settings/theme')) ?? 'dark',
    code: '',
  })

  const reloadFile = () => {
    fetch(`${window.location.href}/repo/${state.file}`)
      .then(res => res.text())
      .then(txt => setState(s => ({ ...s, code: txt })))
      .catch(() => console.error(`failed to load ${state.file}`))
  }

  useEffect(() => {
    window.localStorage.setItem('settings/theme', state.theme)
  }, [state.theme])

  useEffect(() => {
    if (!state.file) return
    const saved = window.localStorage.getItem(`file/${state.file}`)
    if (saved !== null) {
      setState(s => ({ ...s, code: saved }))
      return
    }
    reloadFile()
  }, [state.file])

  useEffect(() => {
    if (!state.file) return
    window.localStorage.setItem(`file/${state.file}`, state.code)
  }, [state.file, state.code])

  return (
    <Context.Provider value={{ state, setState, reloadFile }}>
      {children}
    </Context.Provider>
  )
}

export function useTheme(): {
  theme: Theme,
  setTheme: (newTheme: Theme) => void,
} {
  const { state, setState } = useContext(Context)
  return {
    theme: state.theme,
    setTheme: newTheme => setState({ ...state, theme: newTheme }),
  }
}

export function useFile(): {
  file: string | undefined,
  setFile: (newFile: string | undefined) => void,
  reloadFile: () => void,
} {
  const { state, setState, reloadFile } = useContext(Context)
  return {
    file: state.file,
    setFile: newFile => setState({ ...state, file: newFile }),
    reloadFile,
  }
}

export function useCode(): {
  code: string,
  setCode: (newCode: string) => void,
} {
  const { state, setState } = useContext(Context)
  return {
    code: state.code,
    setCode: newCode => setState({ ...state, code: newCode }),
  }
}
